.breadcrumb{
    line-height:40px;
    background-image: -webkit-gradient(linear, 0 0, 0 100%, from(#fff), to(#efefef));
    background-image: linear-gradient(to bottom, #fff, #efefef);
    background-image: moz-linear-gradient(to bottom, #fff, #efefef);
    list-style:none;
    top: 44px;

  .item{
    padding:0 15px;
    font-size:12px;
    float:left;
    background:url(data:image/.png;base64,) no-repeat;
    background-size:17px 40px;
    color:#3877d6;

    &.active{
      color:#292929;
      background:none;
    }
  }
}

